.login-box {
  background: #FFF;
  border-radius: 5px;
  padding: 15px;
}
.input-box__item {
  margin: 10px;
  position: relative;
  padding-top: 5px;
}
.input-box__item input {
    height: 50px;
    width: 100%;
    outline: none;
    border: none;
    border: 1px solid #DCDCDC;
