}
.input-box__item label {
    display: inline-block;
    color: #8B8B8B;
    position: absolute;
    left: 10px;
    top: 35px;
    cursor: text;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    padding: 0 5px;
}
