/*--reset code--*/
body {
    background: url(/static/img/banner2.e8c64b7.jpg) 0px 0px no-repeat;
    background-position: 50% 50%;
    background-size: cover;
	background-attachment:fixed;
	margin:0;
	padding:0;
}
p{
	font-family: 'PT Sans Narrow', sans-serif;
}
h1,h2,button{

	font-family: 'PT Sans Narrow', sans-serif;
}
span.simply-amount,span.simply-word{
	font-family: 'Montserrat', sans-serif;
}
.clear{
	clear:both;
}
footer{
    border-top: 1px solid #DCDCDC;
	font-family: 'Montserrat', sans-serif;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
/*--/reset code--*/
.agile-countdown h1 {
    font-size: 50px;
    font-weight: 500;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 40px 0 40px 0;
    padding: 0;
    color: white;
}
.agile-subtitle h2 {
    font-size: 25px;
    font-weight: 500;
    text-align: center;
    text-transform: capitalize;
    letter-spacing: 2px;
    margin: 100px 0 0 0;
    padding: 0;
    color: white;
}
p {
   font-size: 15px;
    font-weight: 500;
    text-align: center;
    text-transform: capitalize;
    letter-spacing: 1px;
    margin: 0 0 30px;
    padding: 0;
    color: white;
    line-height: 2;
}
.agile-timer {
    width: 50%;
    margin: 0 auto;
}
span.simply-amount {
    font-size: 40px;
    font-weight: 500;
    text-align: center;
    letter-spacing: 5px;
    margin: 0;
    line-height: 1.5;
}
span.simply-word {
    font-size: 15px;
    text-transform: capitalize;
    letter-spacing: 1px;
    margin: 0;
    padding: 0;
}
.agile-fields {
   width:42%;
    margin: 0 auto;
}
input[type="email"] {
    font-size: 15px;
    font-weight: 500;
    text-align: left;
    text-transform: capitalize;
    letter-spacing: 1px;
    width: 100%;
    margin: 0 auto;
    padding: 10px;
    background: #fff;
    border: 2px solid #d9d9de;
    outline: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
button {
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 4px;
    margin: 35px auto;
    padding: 10px;
    /* width: 25%; */
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: none;
    background: rgba(8, 151, 206, 0.81);
    cursor: pointer;
    outline: none;
    color: white;
    display: block;
}
.social-icons {
    text-align:center;
}
.social-icons ul {
    list-style-type: none;
    margin: 3em 0 0 0;
	padding:0;
}
.social-icons li {
    display: inline-block;
    padding:0 5px;
}
.social-icons a {
    text-decoration: none;
    color: white;
}
.social-icons i.fa.fa-facebook, i.fa.fa-twitter, i.fa.fa-google-plus, i.fa.fa-linkedin {
    font-size: 15px;
    text-align: center;
    width: 30px;
    height: 30px;
    background: none;
    color: #f7f0f0;
    line-height: 2;
    border: 2px solid #fff;
}
i.fa.fa-facebook:hover{
	background:#3b5998;
	color:white;
	-webkit-transition:0.5s ease-in;
	transition:0.5s ease-in;
}
i.fa.fa-twitter:hover{
	background:#1da1f2;
	color:white;
	-webkit-transition:0.5s ease-in;
	transition:0.5s ease-in;
}
i.fa.fa-google-plus:hover{
	background:#dd4b39;
	color:white;
	-webkit-transition:0.5s ease-in;
	transition:0.5s ease-in;
}
.simply-countdown-losange {
  overflow: visible;
  display: table;
  margin: auto;
  font-family: 'Arial', sans-serif;
}
.simply-countdown-losange > .simply-section {
	width: 100px;
    height: 100px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    float: left;
    margin: 0 41px 100px 41px;

}
.simply-countdown-losange > .simply-section > div {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.simply-countdown-losange > .simply-section > div .simply-amount,
.simply-countdown-losange > .simply-section > div .simply-word {
  display: block;
  color: #fff;
  font-weight: bold;
  text-align: center;
}
footer {
    font-size: 13px;
    font-weight: 500;
    text-align: center;
    text-transform: capitalize;
    letter-spacing: 1px;
    margin: 30px 0 0 0;
    color: #fff;
	padding-bottom: 25px;
}
footer a {
    font-size: 13px;
    font-weight: 500;
    text-align: center;
    text-transform: capitalize;
	text-decoration:none;
    letter-spacing: 1px;
    margin: 30px 0 0 0;
    color: #ffe000;
}
